header{position:relative}header .empty h2{font-size:20px;margin-bottom:20px}header .summa{float:left;width:100%;font-weight:600;font-size:20px;margin-bottom:20px}header .logo{font-weight:600;font-size:26px}header .nav{float:right;list-style:none}header .nav li{display:inline;margin-left:25px;cursor:pointer;font-size:15px;font-weight:600;transition:opacity 500ms ease}header .nav li:hover{opacity:.5}header .shopCartButton{float:right;cursor:pointer;font-size:20px;transition:color,transform 500ms ease}header .shopCartButton:hover,header .shopCartButton.active{color:red !important;transform:scale(1.5)}header .shopCart{position:absolute;top:30px;right:0;width:450px;min-height:100px;background:#258bad;box-shadow:-20px 20px 22px 12px rgba(7,7,7,.23);z-index:1000;padding:20px;padding-bottom:0;border-radius:15px}header .presentation{margin:50px 0;background:url(./../../assets/banner.jpg) no-repeat;width:100%;height:500px;background-size:cover;background-position:center center;position:relative;border-radius:15px}header .presentation::after{content:"Лучшая электроника для вашей семьи";color:#faebd7;position:absolute;top:100px;left:50px;width:370px;font-size:40px}header .presentation::before{content:"по низкой цене";color:#faebd7;position:absolute;top:260px;left:50px;width:350px;font-size:35px}